/**
 * Home Component typescript file
 */
import { Component, ViewEncapsulation, ElementRef, ViewChild, HostListener } from '@angular/core';
import { ProductService } from './../services/ProductService';
import { ProductDetailsService } from './../services/ProductDetailsService';
import { ProductDetailModel } from './../models/ProductDetailModel';
import { PictureModel } from './../models/ProductDetailModel';
import { ProductAttribute } from './../models/ProductDetailModel';
import { Category } from './../models/Category';
import { Product } from './../models/Product';
import { StatusCodeEnum } from './../enums/StatusCodeEnum';
import { Modal } from 'ng2-modal';
import { KeyValuePairDTO } from './../models/KeyValuePairDTO';
import { AddToCartDTO } from './../models/AddToCartDTO';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { User } from './../models/User';
import { ShoppingCartService } from './../services/ShoppingCartService';

declare var jQuery: any;

@Component({
  selector: 'home',
  styles: [require('./home.component.scss')],
  template: require('./home.template.html'),
  providers: [ProductService, ProductDetailsService, ShoppingCartService],
  encapsulation: ViewEncapsulation.None
})
export class Home {
  public router:Router;
  @ViewChild('productModal')
  private productModal: Modal;
  valuea: string;
  valueb: string;
  categories: Category[];
  topCategories: Category[] = [];
  dropdownCategories: Category[] = []
  products: Product[];
  public user: User;
  /*
  productModal
  */
  public keys: any[] = [];
  public values: any[] = [];
  public addToCartDTO: AddToCartDTO;
  public keyValuePair: KeyValuePairDTO;
  public errors:any[];

  public product: ProductDetailModel;
  productId: number = 0;
  public productprice: any;
  public producttitle: any;
  public productimages: PictureModel[] = [];
  public oldprice: any;
  public mainimage: any;
  public productdescription: any;
  public discountpercentage: number;
  public ProductAttributes: ProductAttribute;
  /*
  end
  */
  /*
  Pagination Attributes
  * */
  public pageNumber:number = 0;
  public pageSize:number = 25;
  public categoryId:number = -1;
  /**
   * end
   */
  constructor(public _productService: ProductService, public _productdetail: ProductDetailsService, router: Router, public _shoppingCartService: ShoppingCartService) {
    this.user = JSON.parse(localStorage.getItem('user'));
    this.router = router;
    this._productService.getAllCategories().subscribe(
      a => {
        if (a.code == StatusCodeEnum.SUCCESS) {
          this.categories = a.data;
          for (var i = 0; i < this.categories.length; i++) {
            if (i < 7) {
              this.topCategories.push(this.categories[i]);
            }
            else {
              this.dropdownCategories.push(this.categories[i]);
            }
          }
        }
      }
    );
    this.populateProducts(this.categoryId, 0, this.pageSize);
  }

  populateProducts(categoryId, pageNumber, pageSize) {
    this._productService.getAllProducts(categoryId, pageNumber, pageSize).subscribe(
      a => {
        if (a.code == StatusCodeEnum.SUCCESS) {
          if(pageNumber == 0){
            if(a.data.length == 0){
              jQuery('.no-products').show();
              this.products = a.data;
            }
            else{
              jQuery('.no-products').hide();
              this.products = a.data;
            }
          }
          else{
            if(a.data.length != 0){
              for(var i = 0;i<a.data.length; i++){
                this.products.push(a.data[i]);
              }
            }
            else{
              jQuery('.loader-products').hide();
            }
          }
        }
      }
    );
  }

  onCategoryClick(categoryId) {
    this.categoryId = categoryId;
    this.pageNumber = 0;
    this.populateProducts(this.categoryId, 0, this.pageSize);
  }
  makeKeyValue(key, value) {
    this.keys.push(key);
    this.values.push(value);
  }
  onBuyNow() {
    jQuery('#addToCartErrors').hide();
    this.addToCartDTO = new AddToCartDTO();
    for (var i = 0; i < this.keys.length; i++) {
      this.keyValuePair = new KeyValuePairDTO();
      this.keyValuePair.keys = this.keys[i];
      this.keyValuePair.value = this.values[i];
      this.addToCartDTO.keyValuePairDtoArray.push(this.keyValuePair);
    }
    this.user = JSON.parse(localStorage.getItem('user'));
    this.addToCartDTO.customerId = this.user.Id;
    this.addToCartDTO.productId = this.productId;
    this.addToCartDTO.shoppingCartTypeId = 1;
    this.addToCartDTO.quantity = 1;
    this._shoppingCartService.addToCart(this.addToCartDTO).subscribe(
      a => {
        if (a.success == true) {
          jQuery("body").css('overflow', 'scroll');
          this.router.navigate(['/app/cart']);
        }
        else{
          console.log(a);
          jQuery('#addToCartErrors').show();
          this.errors = a.message;
        }
      }
    );
  }

  rightClick(event, productId) {
    console.log(event);
    if (event.which == 3) {        //Right click event
      window.open('#/app/product-detail?productId=' + productId, '_newtab'); // To open in new tab
    }
    else {
      console.log(productId);
      this._productdetail.getProductDetails(productId).subscribe(
        a => {
          this.product = a.data;
          this.productId = productId;
          this.productdescription = this.product.FullDescription;
          this.productprice = this.product.ProductPrice.PriceValue;
          this.producttitle = this.product.Name;
          this.productimages = this.product.PictureModels;
          this.oldprice = this.product.ProductPrice.OldPrice;
          this.mainimage = this.productimages[0].FullSizeImageUrl;
          this.ProductAttributes = this.product.ProductAttributes;
          console.log(this.mainimage);
          if (this.oldprice == 0 || this.oldprice == null) {
            this.discountpercentage = 0;
          }
          else {

          }
          jQuery(".prod-detail-modal").fadeIn();
          jQuery("body").css('overflow', 'hidden');
        }
      );
    }
  }
  onWishList() {
    jQuery('#addToCartErrors').hide();
    this.addToCartDTO = new AddToCartDTO();
    this.user = JSON.parse(localStorage.getItem('user'));
    this.addToCartDTO.customerId = this.user.Id;
    this.addToCartDTO.productId = this.productId;
    this.addToCartDTO.shoppingCartTypeId = 2;
    this.addToCartDTO.quantity = 1;
    this._shoppingCartService.addToCart(this.addToCartDTO).subscribe(
      a => {
        if (a.success == true) {
          console.log('Added to Wish List');
          //this.router.navigate(['/app/cart']);
        }
        else{
          console.log(a);
          jQuery('#addToCartErrors').show();
          this.errors = a.message;
        }
      }
    );
  }
  ngOnInit(): void {
    jQuery("body").on("click", ".close-modal", function (e) {
      var $this = jQuery(this);
      e.preventDefault();
      jQuery(this).parents(".prod-detail-modal").fadeOut();
      jQuery("body").css('overflow', 'scroll');
      jQuery(".zoomContainer").html("");
    });
    
  }
  @HostListener("window:scroll", ["$event"])
  onWindowScroll() {
    //In chrome and some browser scroll is given to body tag
    let pos = (document.documentElement.scrollTop || document.body.scrollTop) + document.documentElement.offsetHeight;
    let max = document.documentElement.scrollHeight;
    // pos/max will give you the distance between scroll bottom and and bottom of screen in percentage.
    if (pos == max) {
      console.log('Pagination Called');
      this.pageNumber = this.pageNumber + 1;
      this.populateProducts(this.categoryId, this.pageNumber, this.pageSize);
    }
  }
}



